

const VAPID_PUBLIC_KEY =
  "BBBhf0wej5fkbxA9XcDE3fvbJRWCdTL61plMN8KdtMkWeKMzVF7b-5YBfLH__vJpq5CqrFqy3ittbRr4PtNCWcM";
import { useState } from "react";
import { supabase } from "../database/supabaseClient";


function urlBase64ToUint8Array(base64String: string) {
  const padding = "=".repeat((4 - (base64String.length % 4)) % 4);
  const base64 = (base64String + padding).replace(/-/g, "+").replace(/_/g, "/");
  const raw = atob(base64);
  return Uint8Array.from([...raw].map(ch => ch.charCodeAt(0)));
}

export function usePushSubscription() {
  const [status, setStatus] = useState("");

  async function subscribe(data: { name?: string; email?: string; device?: string }) {
    try {
      setStatus("Checking permission…");

      // 1. Ask permission
      const perm = await Notification.requestPermission();
      if (perm !== "granted") {
        throw new Error("Permission denied by browser");
      }

      // 2. Wait for SW
      const reg = await navigator.serviceWorker.ready;

      // 3. Check for an existing subscription FIRST
      const existing = await reg.pushManager.getSubscription();
      if (existing) {
        setStatus("Already subscribed — updating db…");
        await supabase.from("push_subscriptions").insert({
          subscription: existing.toJSON(),
          name: data.name || null,
          email: data.email || null,
          device: data.device || "primary phone"
        });
        return existing;
      }

      // 4. Subscribe
      setStatus("Subscribing…");
      const sub = await reg.pushManager.subscribe({
        userVisibleOnly: true,
        applicationServerKey: urlBase64ToUint8Array(VAPID_PUBLIC_KEY)
      });

      // 5. Save
      const { error } = await supabase.from("push_subscriptions").insert({
        subscription: sub.toJSON(),
        name: data.name || null,
        email: data.email || null,
        device: data.device || "primary phone"
      });

      if (error) throw new Error(error.message);

      setStatus("Subscribed!");
      return sub;

    } catch (err: any) {
      setStatus("Failed: " + err.message);
      throw err;
    }
  }

  return { subscribe, status };
}

