import React, { useEffect, useState } from "react";
import { usePushSubscription } from "../hooks/usePushNotificationSubscription";
import { supabase } from "../database/supabaseClient";

/* ----------------------------------------------------
   SAFE PLATFORM DETECTION (SSR-PROOF)
---------------------------------------------------- */
function safeIsIos() {
  if (typeof navigator === "undefined") return false;
  return /iphone|ipad|ipod/i.test(navigator.userAgent);
}

function safeIsStandalone() {
  if (typeof window === "undefined" || typeof navigator === "undefined") return false;
  return (
    window.matchMedia("(display-mode: standalone)").matches ||
    (navigator as any).standalone === true
  );
}

function safeIsAndroid() {
  if (typeof navigator === "undefined") return false;
  return /android/i.test(navigator.userAgent);
}

/* ----------------------------------------------------
   INLINE iOS INSTALL OVERLAY
---------------------------------------------------- */
function IosInstallOverlay({ onClose }: { onClose: () => void }) {
  return (
    <div
      style={{
        position: "fixed",
        inset: 0,
        background: "rgba(0,0,0,0.65)",
        color: "white",
        padding: "2rem",
        zIndex: 9999
      }}
    >
      <div
        style={{
          maxWidth: 400,
          margin: "0 auto",
          background: "#222",
          padding: 20,
          borderRadius: 12
        }}
      >
        <h2>Install this App</h2>
        <p>To enable notifications on your iPhone:</p>

        <ol>
          <li>
            Tap the <strong>Share</strong> button{" "}
            <span style={{ fontSize: "1.3rem" }}>⬆️</span>
          </li>
          <li>
            Choose <strong>Add to Home Screen</strong>
          </li>
          <li>Re-open the app from your home screen</li>
        </ol>

        <button
          onClick={onClose}
          style={{
            marginTop: 20,
            padding: "8px 14px",
            background: "#555",
            color: "white",
            borderRadius: 6
          }}
        >
          Got it
        </button>
      </div>
    </div>
  );
}

/* ----------------------------------------------------
   SEND PUSH HELPER
---------------------------------------------------- */
async function sendPushMessage(title: string, body: string) {
  const { data, error } = await supabase
    .from("push_subscriptions")
    .select("subscription")
    .order("created_at", { ascending: false })
    .limit(1)
    .maybeSingle();

  if (error) throw new Error(error.message);
  if (!data) throw new Error("No subscriptions found.");

  const subscription = data.subscription;

  const res = await fetch("https://auth.tinhhoaviet.org.vn/push", {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({
      secret: "Zf8uP1q9R4x!bA72Lk@cM39vD$wQeT6",
      subscription: subscription,
      title,
      body
    })
  });

  const txt = await res.text();
  if (!res.ok) throw new Error(txt);

  return "Push sent!";
}

/* ----------------------------------------------------
   MAIN PAGE (ALL-IN-ONE)
---------------------------------------------------- */
export default function PwaSubscribePage() {
  const { subscribe, status } = usePushSubscription();

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [device, setDevice] = useState("primary phone");

  const [pushTitle, setPushTitle] = useState("Test Message");
  const [pushBody, setPushBody] = useState("Hello from THV!");
  const [pushStatus, setPushStatus] = useState("");

  const [showIosPrompt, setShowIosPrompt] = useState(false);

  /* Register service worker for /pwa/ scope */
  useEffect(() => {
    if (typeof navigator !== "undefined" && "serviceWorker" in navigator) {
      navigator.serviceWorker.register("/pwa/service-worker.js", {
        scope: "/pwa/"
      });
    }
  }, []);

  /* Detect iOS + not PWA */
  useEffect(() => {
    if (safeIsIos() && !safeIsStandalone()) {
      setShowIosPrompt(true);
    }
  }, []);

  async function handleSubscribe() {
    await subscribe({ name, email, device });
  }

  async function handleSendPush() {
    setPushStatus("Sending…");
    try {
      const msg = await sendPushMessage(pushTitle, pushBody);
      setPushStatus(msg);
    } catch (err: any) {
      setPushStatus(err.message);
    }
  }

  return (
    <div style={{ padding: "2rem", maxWidth: 600, margin: "0 auto" }}>
      <h1>Web Push Notifications</h1>

      {safeIsAndroid() && (
        <p style={{ color: "#0a0" }}>
          Android detected. Push works directly in browser. No install required.
        </p>
      )}

      {safeIsIos() && safeIsStandalone() && (
        <p style={{ color: "#0a0" }}>
          iOS PWA mode detected. Push subscription is supported.
        </p>
      )}

      {safeIsIos() && !safeIsStandalone() && (
        <p style={{ color: "orange" }}>
          iPhone detected. Install the app first to enable notifications.
        </p>
      )}

      <h2>Subscribe</h2>

      <label>Name</label>
      <input
        value={name}
        onChange={e => setName(e.target.value)}
        placeholder="Your name"
        style={{ width: "100%", marginBottom: "1rem" }}
      />

      <label>Email</label>
      <input
        value={email}
        onChange={e => setEmail(e.target.value)}
        placeholder="you@example.com"
        style={{ width: "100%", marginBottom: "1rem" }}
      />

      <label>Device</label>
      <input
        value={device}
        onChange={e => setDevice(e.target.value)}
        placeholder="primary phone"
        style={{ width: "100%", marginBottom: "1rem" }}
      />

      <button onClick={handleSubscribe}>Subscribe Now</button>

      {status && <p>{status}</p>}

      <hr style={{ margin: "2rem 0" }} />

      <h2>Send Push</h2>

      <label>Title</label>
      <input
        value={pushTitle}
        onChange={e => setPushTitle(e.target.value)}
        style={{ width: "100%", marginBottom: "1rem" }}
      />

      <label>Message</label>
      <textarea
        value={pushBody}
        onChange={e => setPushBody(e.target.value)}
        rows={3}
        style={{ width: "100%", marginBottom: "1rem" }}
      />

      <button onClick={handleSendPush}>Send Push</button>

      {pushStatus && <p>{pushStatus}</p>}

      {showIosPrompt && <IosInstallOverlay onClose={() => setShowIosPrompt(false)} />}
    </div>
  );
}
